import { Routes, Route } from "react-router-dom";

import Page from "./Page";
import NotFound from "../pages/NotFound";
import Home from "../pages/Home";
import Ratings from "../pages/Ratings";
import Gallery from "../pages/Gallery";
import Registry from "../pages/Registry";
import Old from "../pages/Old";

export default function Router() {
	return (
		<Routes>
			<Route
				path="/*"
				element={
					<Page title="  &bull; 404">
						<NotFound />
					</Page>
				}
			/>

			<Route
				path="/"
				element={
					<Page title="  &bull; ">
						<Home />
					</Page>
				}
			/>

			<Route
				path="/ratings"
				element={
					<Page title="  &bull; ">
						<Ratings />
					</Page>
				}
			/>

			<Route
				path="/gallery"
				element={
					<Page title="  &bull; ">
						<Gallery />
					</Page>
				}
			/>

			<Route
				path="/registry"
				element={
					<Page title="  &bull;  ">
						<Registry />
					</Page>
				}
			/>

			<Route
				path="/old"
				element={
					<Page title="  &bull; ">
						<Old />
					</Page>
				}
			/>
		</Routes>
	);
}
